* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --primary-color: #eeeeee;
    --secondary-color: #222831;
    --primary-color-shade: #f5f5f5;
}

html {
    font-size: 62.5%;
}

body {
    font-size: 1.6rem;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    min-height: 100vh;

    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: min-content 1fr min-content;
}

input[type="text"] {
    width: 100%;
    font-size: 1.6rem;
    padding: 0.2em 0.4em;
}

input[type="date"] {
    padding: 0;
    margin: 0;
    width: 100%;
}

/* header */
header {
    color: var(--primary-color);
    background-color: var(--secondary-color);
    padding: 2rem 4rem;
    grid-area: 1 / 1 / 2 / 3;

    display: flex;
    align-items: center;
    gap: 1rem;
}

header img {
    filter: invert(100%) sepia(4%) saturate(18%) hue-rotate(54deg)
        brightness(104%) contrast(104%);
    width: 4%;
    aspect-ratio: 1/1;
}

/* aside */
aside {
    padding: 2rem;
    grid-area: 2 / 1 / 3 / 2;
    background-color: var(--primary-color);
    display: grid;
    grid-template-rows: 1fr 2fr;
}

aside nav {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}

button {
    padding: 0.6em 1em;
    font-size: 1.6rem;
    font-size: 1.6rem;
    border: none;

    display: flex;
    align-items: center;
    gap: 0.5em;
}

nav button:focus,
button:hover {
    filter: brightness(90%);
}

nav button:focus {
    font-weight: bold;
}

button img {
    width: 1.5em;
    aspect-ratio: 1/1;
}

.project-nav {
    display: flex;
    flex-direction: column;
}

.project-nav h2 {
    margin-bottom: 1rem;
}

.prj-btn .delete-project-btn {
    margin-left: auto;
}

/* main  */
main {
    background-color: var(--primary-color-shade);
    padding: 2rem 10rem 2rem 10rem;

    grid-area: 2 / 2 / 3 / 3;

    display: flex;
    flex-direction: column;
    gap: 3rem;
}

main h2 {
    padding-left: 2rem;
}
h2:hover {
    filter: brightness(90%);
}

main button {
    background-color: var(--primary-color-shade);
}

.todo-input-div {
    display: flex;
    flex-direction: column;
}

.todo-input-div .utility-div {
    display: flex;
    align-items: center;
}

.utility-div .save {
    margin-left: auto;
}

.todo-div {
    background-color: var(--primary-color);
    width: 100%;
    display: flex;
    align-items: center;    
}

.todo-div:hover {
    filter: brightness(90%);
}

.todo-div .todo-finished-btn:hover {
    filter: none;
}

.todo-div .todo-title {
    flex-grow: 1;
}

/* .todo-div .todo-important {
    margin: 0 1rem;
    width: 1.5em;
    aspect-ratio: 1/1;
} */

/* footer */
footer {
    background-color: var(--secondary-color);
    text-align: center;
    color: var(--primary-color);
    font-size: 1.6rem;
    padding: 0.5rem 0;
    grid-area: 3 / 1 / 4 / 3;
}

/* date picker styling */
.datepicker-div {    
    padding: 0.6em 1em;
    font-size: 1.6rem;    

    background-color: var(--primary-color-shade);
    display: flex;
    align-items: center;    
}
.datepicker-div:hover {
    filter: brightness(90%) !important;
}

.datepicker-toggle {
    display: inline-block;
    position: relative;
    width: 1.5em;
    aspect-ratio: 1/1;
}
.datepicker-toggle-button {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.datepicker-input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    /* cursor: pointer; */
    box-sizing: border-box;
}
.datepicker-input::-webkit-calendar-picker-indicator {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    /* cursor: pointer; */
}

